import React, { Component } from 'react';
import './About.css';

export default class About extends Component {
	render() {
		return (
			<div className="About text-center">
				<div className="container fluid">
					<div className="row">
						<h2> About Me </h2>
					</div>
					<div className="row">
						<div className="col-md-1 col-sm-1">
						</div>
						<div className="col-md-10 col-sm-10 text-block">
							<p>
								Hi my name is Erik Dussel and welcome to my Portfolio.
							</p>
						</div>
						<div className="col-md-1 col-sm-1">
						</div>
					</div>
				</div>
			</div>
		);
	}
}


